@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title{z-index:4;font-size:32px;line-height:38px;text-align:center;padding:20px 0}

.header{display:grid;grid-gap:10px}@media screen and (min-width: 900px){.header{grid-template-columns:repeat(auto-fill, minmax(350px, 1fr));grid-template-columns:auto 1fr}}

input[type='text']{color:rgba(var(--blue), 1);background-color:rgba(var(--blue), var(--light));border:2px solid rgba(var(--dark), var(--light));border-radius:20px;font-size:16px;line-height:20px;padding:5px 10px 5px 40px;-webkit-appearance:none;-moz-appearance:none;appearance:none;width:100%;box-sizing:border-box;position:relative}input[type='text']::-webkit-input-placeholder{color:rgba(var(--dark), var(--saturated))}input[type='text']:-ms-input-placeholder{color:rgba(var(--dark), var(--saturated))}input[type='text']::-ms-input-placeholder{color:rgba(var(--dark), var(--saturated))}input[type='text']::placeholder{color:rgba(var(--dark), var(--saturated))}input[type='text']+.icon svg path:last-child{fill:rgba(var(--dark), var(--saturated))}input[type='text']:placeholder-shown{background-color:rgba(var(--white), var(--light))}input[type='text']:focus,input[type='text']:active,input[type='text']:hover{outline:none;border-color:rgba(var(--blue), var(--saturated));background-color:rgba(var(--blue), var(--light))}input[type='text']:focus::-webkit-input-placeholder, input[type='text']:active::-webkit-input-placeholder, input[type='text']:hover::-webkit-input-placeholder{color:rgba(var(--blue), var(--saturated))}input[type='text']:focus:-ms-input-placeholder, input[type='text']:active:-ms-input-placeholder, input[type='text']:hover:-ms-input-placeholder{color:rgba(var(--blue), var(--saturated))}input[type='text']:focus::-ms-input-placeholder, input[type='text']:active::-ms-input-placeholder, input[type='text']:hover::-ms-input-placeholder{color:rgba(var(--blue), var(--saturated))}input[type='text']:focus::placeholder,input[type='text']:active::placeholder,input[type='text']:hover::placeholder{color:rgba(var(--blue), var(--saturated))}input[type='text']:focus+.icon svg path:last-child,input[type='text']:active+.icon svg path:last-child,input[type='text']:hover+.icon svg path:last-child{fill:rgba(var(--blue), var(--saturated))}.search{width:100%;max-width:380px}.input-group{position:relative}.icon{position:absolute;left:10px;top:5px}

.regions{display:flex;padding:0 0 20px 0;overflow:scroll}@media screen and (min-width: 900px){.regions{justify-self:end}}.regions .input-group:last-of-type{padding-right:10px}input[type="checkbox"]{opacity:0;position:fixed;width:0}input[type="checkbox"]+label{border:2px solid rgba(var(--dark), var(--light));color:rgba(var(--dark), var(--saturated));background-color:transparent;font-size:16px;line-height:20px;padding:5px 15px;border-radius:23px;display:inline-block;margin-right:5px}input[type="checkbox"]+label:focus,input[type="checkbox"]+label:hover{outline:none;color:rgba(var(--blue), var(--saturated));border-color:rgba(var(--blue), var(--saturated))}input[type="checkbox"]:checked+label{background:rgba(var(--blue), var(--light));color:rgba(var(--blue), var(--saturated));border-color:rgba(var(--blue), var(--light))}input[type="checkbox"]:checked+label:hover{border-color:rgba(var(--blue), var(--saturated))}input[type="checkbox"]:focus+label{color:rgba(var(--blue), var(--saturated));background:rgba(var(--blue), var(--light));border-color:rgba(var(--blue), var(--saturated))}

.list{display:grid;grid-gap:26px;padding:10px 0 100px 0;max-width:350px;margin:auto}.list__item a{display:grid;overflow:hidden;-webkit-transition:all 0.25s linear;transition:all 0.25s linear;background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(var(--white), 0)), to(rgba(var(--white), var(--saturated))));background-image:linear-gradient(to bottom, rgba(var(--white), 0), rgba(var(--white), var(--saturated)));padding:0 4px 20px 4px;border-radius:8px;height:100%;box-sizing:border-box}.list__item a:hover,.list__item a:focus{outline:none;background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(var(--white), 0)), to(rgba(var(--lightBlue), var(--light))));background-image:linear-gradient(to bottom, rgba(var(--white), 0), rgba(var(--lightBlue), var(--light)))}.list__item a:visited{color:rbg(var(--dark))}@media screen and (min-width: 780px){.list{max-width:1200px;grid-template-columns:repeat(auto-fill, minmax(350px, 1fr))}}.country-info{display:grid;grid-gap:3px;padding:15px 20px}.country-info h3{margin-bottom:15px}.flag{width:100%;height:50vw;border-top-left-radius:8px;border-top-right-radius:8px;overflow:hidden}@media screen and (min-width: 500px){.flag{height:200px}}.flag img{object-fit:cover;height:100%}img{width:100%;height:100%;display:block;object-fit:cover}a{text-decoration:none;color:rbg(var(--dark))}.empty{font-size:30px;line-height:40px;text-align:center}

.borders{display:grid;grid-gap:10px}.borderList{display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fill, minmax(150px, 1fr))}.borderList__border{display:grid;grid-template-columns:auto 1fr;grid-gap:10px;align-items:center;border:1px solid rgba(var(--blue), var(--light));background:rgba(var(--blue), var(--light));padding:0 5px 0 0;border-radius:20px;overflow:hidden;font-size:16px;position:relative}.borderList__border:after{content:'';position:absolute;top:0;top:0;right:0;bottom:0;left:0;z-index:-1;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:center left;transform-origin:center left;background:rgba(var(--blue), var(--saturated));-webkit-transition:-webkit-transform 0.25s ease-in-out;transition:-webkit-transform 0.25s ease-in-out;transition:transform 0.25s ease-in-out;transition:transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out}.borderList__border span{text-transform:uppercase;position:relative;display:inline-block;word-break:break-word}.borderList__border:hover,.borderList__border:focus{outline:none;color:rgba(var(--white), var(--saturated))}.borderList__border:hover:after,.borderList__border:focus:after{-webkit-transform:scaleX(1);transform:scaleX(1)}.country{max-width:900px;margin:0 auto;padding:20px}.content__top,.content__bottom{margin:20px 0;display:grid;grid-gap:20px}@media screen and (min-width: 600px){.content{display:grid;grid-template-columns:350px 1fr;grid-gap:20px;align-items:start}}img{width:100%;height:auto}.back-button{display:flex;align-items:center;padding:10px 15px 10px 5px;font-weight:bold;font-size:16px;line-height:20px;text-transform:uppercase;color:rgba(var(--blue), var(--saturated));background:rgba(var(--blue), var(--light));border-radius:7px;border:2px solid transparent}.back-button:hover,.back-button:focus{outline:none;color:rgba(var(--white), var(--saturated));background:rgba(var(--blue), var(--saturated))}.back-button:hover path:last-child,.back-button:focus path:last-child{fill:rgba(var(--white), var(--saturated))}.back-button svg{margin-right:5px}.mini-flag{height:30px;width:30px;border-radius:50%;overflow:hidden;border:1px solid rgba(var(--gray), var(--saturated))}.mini-flag img{object-fit:cover;height:100%}.mini-list span{margin-right:5px}.mini-list span:after{content:','}.mini-list span:last-of-type:after{content:none}.inner{padding:0 10px}.mb-10{margin-bottom:10px}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}div{box-sizing:border-box}input{-webkit-appearance:none;border-radius:0;margin:0}body{background:rgba(207, 216, 220, 0.2);background:rgba(var(--gray), var(--light))}.container{padding:15px;max-width:1200px;margin:0 auto}:root{--blue: 41, 98, 255;--dark: 38, 50, 56;--gray: 207, 216, 220;--lightBlue: 68, 138, 255;--white: 255, 255, 255;--light: 0.2;--saturated: 1}body{font-family:'Roboto Slab', serif;font-weight:400;font-size:18px;line-height:22px}h2{font-size:32px;line-height:38px}h3{font-size:24px;line-height:26px}b,.bold{font-weight:700}

